* {
   font-family: 'Quicksand', sans-serif;
}

/* Top bar */
.topbar {
   padding-top: 16px;
   padding-bottom: 10px;
   font-size: 2.2em;
   text-align: center;
   font-family: 'Lato', sans-serif;
}

.topbar a {
   color: #000;
}

.topbar a:hover {
   text-decoration: none;
}

/* Header */
.jumbotron:hover {
   -webkit-box-shadow: none;
           box-shadow: none;
}

.jumbotron {
   background-image: url('images/rocket.jpg');
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-position: center;
   padding: 5rem 2rem;
   color: #fff;
   -webkit-transition: -webkit-box-shadow .5s ease;
   transition: -webkit-box-shadow .5s ease;
   -o-transition: box-shadow .5s ease;
   transition: box-shadow .5s ease;
   transition: box-shadow .5s ease, -webkit-box-shadow .5s ease;
   -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
           box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.jumbotron-content {
   padding-top: 10px;
}

.avatar {
   -webkit-box-shadow: 0px 0px 5px #fff;
           box-shadow: 0px 0px 5px #fff;
}

.short-bio {
   text-align: left;
   margin: 0;
   padding-top: 16px;
   font-size: 19px;
   font-weight: 300;
}

/* Navigation */
.nav-pills {
   margin-bottom: 25px;
}

.nav-pills .nav-link {
   color: #111;
   outline: none;
   border: 1px solid rgba(64, 64, 64, 0.17);
   -webkit-transition: all .5s ease;
   -o-transition: all .5s ease;
   transition: all .5s ease;
   margin-right: 6px;
   -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0), 0 1px 2px rgba(0,0,0,0.23);
           box-shadow: 0 2px 3px rgba(0, 0, 0, 0), 0 1px 2px rgba(0,0,0,0.23);
}

.nav-pills .nav-link:hover {
   -webkit-box-shadow: none;
           box-shadow: none;
}

.nav-pills .nav-link .active {
   border: none;
}

.tab-content {
   margin-top: 25px;
}

/* About */
.about-bio {
   text-align: justify;
}

/* Experience */
.employment-card {
   height: auto;
   border-radius: 7px;
   background: rgba(245, 245, 245, 0.58);
   /* border: 1px solid rgba(41, 12, 12, 0.17); */
   border-left: 5px solid #29D28B;
   margin-bottom: 15px;
   padding: 10px 20px;
   /* line-height: 15px; */
   -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0), 0 1px 2px rgba(0,0,0,0.23);
           box-shadow: 0 2px 3px rgba(0, 0, 0, 0), 0 1px 2px rgba(0,0,0,0.23);
}

.employment-card p {
   margin-bottom: 5px;
}

.employment-card .company-name {
   font-size: 23px;
}

.badge-success {
   background: #29D28B;
}


